<template>
    <div class="top-space">
        <div @click="btnAdd" :class="isSelected ? 'block is-green' : 'block is-grey'">
            <div class="widget d-flex flex-column posSet">
                <strong class="title">{{ this.name }}</strong>
                <div>
                    <WeatherWidgetAdd v-if="isWeatherWidget" />
                    <HueWidgetAdd v-if="isHueWidget" />
                    <SpotifyWidgetAdd v-if="isSpotifyWidget" />
                    <CalendarWidgetAdd v-if="isCalendarWidget" />
                    <EventWidgetAdd v-if="isEventWidget" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import WeatherWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/WeatherAdd.vue"
import HueWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/HueAdd.vue"
import SpotifyWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/SpotifyAdd.vue"
import CalendarWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/CalendarGridAdd.vue"
import EventWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/EventAdd.vue"


export default {
    name: "genericWidgetAdd",
    data() {
        return {
            id: null,
            name: null,
            bgColor: "#145374",

            isSelected: false,

            isWeatherWidget: false,
            isHueWidget: false,
            isSpotifyWidget: false,
            isCalendarWidget: false,
            isEventWidget: false,
        };
    },
    components: {
        WeatherWidgetAdd,
        HueWidgetAdd,
        SpotifyWidgetAdd,
        CalendarWidgetAdd,
        EventWidgetAdd
    },
    props: {
        widgetInfo: {}
    },
    beforeMount() {
    },
    mounted() {
        this.name = this.widgetInfo.name;
        this.id = this.widgetInfo._id;
        // console.log("Widget : ", this.widgetInfo);

        switch (this.id) {
            case "629a5ed82f6d9bf0c71333f9": //weather
                this.isWeatherWidget = true;
                break;
            case "629a65232f6d9bf0c71333fa": //Hue
                // this.bgColor = "#015FD9"
                this.isHueWidget = true;
                break;
            case "62a38edb9bcab46134b480d6": //Spotify
                this.bgColor = "#1ED760"
                this.isSpotifyWidget = true;
                break;
            case "62a3b1409bcab46134b480d7": //Calendar
                this.isCalendarWidget = true;
                break;
            case "62a4451946d679d1f921c81d": //Event
                this.isEventWidget = true;
                break;
            default:
                break;
        }
    },
    beforeDestroy() {
    },
    methods: {
        btnAdd() {
            this.isSelected = true;

            switch (this.id) {
                case "629a5ed82f6d9bf0c71333f9": //weather
                    this.$router.push({ name: 'WeatherConfig' })
                    break;
                case "629a65232f6d9bf0c71333fa": //Hue
                    this.$router.push({ name: 'HueConfig' })
                    break;
                case "62a38edb9bcab46134b480d6": //Spotify
                    this.$router.push({ name: 'SpotifyConfig' })
                    break;
                case "62a3b1409bcab46134b480d7": //Calendar
                    this.$router.push({ name: 'CalendarGridConfig' })
                    break;
                case "62a4451946d679d1f921c81d": //Event
                    this.$router.push({ name: 'EventConfig' })
                    break;

                default:
                    break;
            }
        },
    },
};
</script>

<style scoped>
.widget {
    border-radius: 12px;
    height: 200px;
    width: 200px;
    background-color: v-bind(bgColor);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
}

.block {
    border-radius: 20px;
    border-width: 4px;
    border-style: solid;
    height: 215px;
    width: 215px;
    /* background-color: #50b8f0; */
    /* color: #ffffff; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.is-grey {
    border-color: rgba(0, 73, 0, 0.356);
}

.is-green {
    border-color: rgb(22, 194, 22);
}

.title {
    position: absolute;
    top: 20px;
    left: 20px;

}

.posSet {
    position: relative;
}

.top-space {
    padding-top: 30px;
}
</style>