<template>
  <div>
    <h1>Signup</h1>
    <form @change="checkForm">
      <label>User name :</label>
      <input v-model="username" type="text" required />
      <label>Email :</label>
      <input v-model="email" type="email" required />
      <label>Password :</label>
      <input v-model="password" type="password" required />
    </form>
    <div v-if="errors.length !== 0" class="alert alert-danger" role="alert">
      <li v-for="item in this.errors" :key="item.id">
        {{ item }}
      </li>
    </div>
    <div v-if="validate" class="alert alert-success" role="alert">
    Signup done! You can login now!
    </div>
    <button @click="submitPress">Submit</button>
  </div>
</template>

<script>
import { defineComponent } from "vue"
import axios from 'axios';

export default defineComponent({
  name: 'SignIn',
  data() {
    return {
      username: null,
      email: null,
      password: null,
      formIsgood: false,

      validate: false,

      errorMessage: null,
      errors: []
    }
  },
  methods: {
    checkForm: function (e) {
      this.errors = []
      if (!this.username) {
        this.errors.push("Username required.");
      }
      if (!this.email) {
        this.errors.push('Email required.');
      } else if (!this.validEmail(this.email)) {
        this.errors.push('Valid email required.');
      }
      if (!this.password) {
        this.errors.push('Password required.');
      } else if (this.password.length < 8) {
        this.errors.push('Valid password required. (8 caracter min.)');
      }

      if (!this.errors.length) {
        this.formIsgood = true
      }
      else {
        // this.errorMessage = this.errors
        this.formIsgood = false
      }
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    submitPress() {
      if (this.formIsgood) {
        axios({
          method: 'post',
          // url: 'http://localhost:8080/api/auth/signup',
          url: "https://www.emuix.com/api/auth/signup",
          data: {
            "username": this.username,
            "email": this.email,
            "password": this.password,
            "roles": ["user"]
          }
        })
          .then((response) => {
            this.validate = true;
            this.apiResp = response.data.message;
          });
      }
      else {
        // this.errorMessage = this.errors;
      }
    },
  },
});
</script>

