<template>
  <div>
    <h1>Login</h1>
    <form>
      <label>Email :</label>
      <input @change="logTest" v-model="email" type="email" required />
      <label>Password :</label>
      <input @change="logTest" v-model="password" type="password" required />
    </form>
    <div v-if="errorMessage" class="alert alert-danger" role="alert">
      {{ this.errorMessage }}
    </div>
    <button @click="submitPress" type="submit">Submit</button>
  </div>
</template>

<script>
import { defineComponent } from "vue";
import axios from "axios";

export default defineComponent({
  name: "SignIn",
  data() {
    return {
      email: null,
      password: null,
      errorMessage: null,
    };
  },
  methods: {
    logTest() {
      console.log(this.email);
      console.log(this.password);
    },
    submitPress() {
      if (this.email && this.password) {
        axios({
          method: "post",
          // url: "http://localhost:8080/api/auth/signin",
          url: "https://www.emuix.com/api/auth/signin",
          data: {
            email: this.email,
            password: this.password,
          },
        })
          .then((response) => {
            this.$router.push('/Home')
            console.log("Then from res : ", response.status);
          })
          .catch((response) => {
            console.log("Then from res : ", response.response.data.message);
            this.errorMessage = response.response.data.message;
          });
      } else{
        this.errorMessage = "All champs is needed!";
      }
    },
  }
});
</script>

<style scope>
.alert {
  margin-top: 30px;
  margin-bottom: -20px;
}

form {
  text-align: left;
}

label {
  color: #aaa;
  display: inline-block;
  margin: 25px 0 15px;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}

input {
  display: block;
  padding: 0.1vh 6px;
  width: 100%;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #ddd;
  color: #555;
}

button {
  margin: 50px;
  padding: 15px 55px;
  border: 0px;
  background: #0052c6;
  color: white;
  font-size: 0.9em;
  border-radius: 30px;
}

button:hover {
  margin: 50px;
  padding: 15px 55px;
  border: 0px;
  background: #0766eb;
  color: white;
  font-size: 0.9em;
}
</style>